@tailwind base;
@tailwind components;
@tailwind utilities;

.input {
  @apply w-full rounded-xl border border-gray-300 dark:border-neutral-700 bg-white dark:bg-neutral-800 px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 transition;
}

.btn-primary {
  @apply bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded-xl transition;
}

.btn-secondary {
  @apply bg-gray-200 dark:bg-neutral-700 hover:bg-gray-300 dark:hover:bg-neutral-600 text-gray-800 dark:text-white font-semibold px-6 py-2 rounded-xl transition;
}
